.main {
    height: 90vh;
    /* margin-bottom: 500px;
    border: 1px solid red; */
}

body {
    background-color: rgba(218, 218, 218, 0.301);
}

.layout {
    display: flex;
}


.sidebar {
    width: 300px;
    background-color: #fff;
    border-radius: 5px;
    margin-right: 10px;
    color: #000;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
}

.profileContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 20px;
}

.profile {
    display: flex;
}

.profileContainer>div>div>p:nth-child(2) {
    font-weight: 600;
    font-size: 20px;
}

.userName {
    display: flex;
    justify-content: start;
    flex-direction: column;
}

.userName p {
    margin: 0;
    padding-left: 20px;
}

.profileContainer img {
    border-radius: 60%;
    width: 60px;
}

.notification {
    /* margin: -20px 0 0 90px; */
    display: flex;
    padding-right: 10px;
    position: relative;
}

.notification i {
    font-size: 30px;
}

.notification span {
    left: 20px;
    top: -10px;
    position: absolute;
}

.logo {
    font-size: 1rem;
    text-align: center;
    margin: 20px 0;
}

.menu {
    margin-top: 30px;
    padding: 0 20px;
}

.menu-item {
    padding: 20px;
    margin: 10px 0;
    border-radius: 10px;
}


.menu-item a {
    text-decoration: none;
    color: #000;
    font-size: 1.2rem;
    font-weight: 500;
}

.menu-item i {
    margin-right: 20px;
    font-size: 1.2rem;
}

.active {
    /* background-color: red; */
}

.active i,
.active Link,
.active a {
    color: rgb(35, 107, 165);
}

.content {
    width: 100%;
    /* margin: 2px 0 0 20px; */
}

.header {
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px grey;
    background-color: #fff;
}

.header-content {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
}

.header-content p {
    margin: 10px 30px 10px 10px;
    text-transform: uppercase;
    font-weight: 500;
}

.header-content i {
    font-size: 1.6rem;
}

.header-content span {
    margin: 0 0 20px 0;
}


.body {
    height: 100%;
    margin-bottom: 100px;
    padding: 20px;
    background: #fff;
    overflow-x: scroll;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
}

/* Mobile  */
.headerMenu i {
    color: #fff !important;
    margin: 5px;
    font-size: 16px;
}

.headerMenu i span {
    margin-left: 5px;
    font-size: 12px;
}

@media screen and (max-width:768px) {
    .body {
        height: auto;
    }
}