.headingTitle {
    font-weight: 600;
    font-family: 'Robot', sans-serif;
    font-size: 1.6rem;
    color: #333333;
}

.headingDesc {
    font-size: 1.2rem;
    color: grey;
}

.card-container {
    width: 350px;
    height: 80px;
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    border-radius: 15px;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.card-container:hover h6 {
    color: white;
}

.card-container::before {
    content: '';
    /* background-color: rgb(2, 100, 40); */
    background-color: var(--clr);
    height: 100px;
    width: 350px;
    position: absolute;
    transition: .4s ease;
    z-index: 1;
    right: 70%;
}

.card-container img,
.card-container h6 {
    z-index: 2;
}

.imgContainer {
    z-index: 2;
    width: 100px;
    /* border: 1px solid red; */
}

.card-container img {
    padding: 30px;
}

.card-container h6 {
    margin-left: 20px;
    text-align: start !important;
    font-weight: 600;
    font-size: .95rem;
}

.card-container:hover::before {
    right: 0%;
}

@media screen and (max-width:768px) {
    .headingTitle {
        font-size: 1.3rem;
        font-weight: 800;
        padding: 0 10px;
    }

    .headingDesc {
        font-size: 1rem;
        padding: 0 10px;
    }
}