.logo {
    font-size: 25px !important;
    margin-right: 40px !important;
}

.navbar-toggler:focus {
    border: 0 !important;
    outline: none !important;
}

.navbar-toggler:focus {
    color: #fff !important;
    outline: none !important;
    border: 0 !important;
}

.searchInput {
    width: 700px;
    display: flex;
    border: 1px solid red;
}

.inputLocation {
    width: 165px;
    position: relative;
    /* border: 1px solid red; */
}

.inputLocation input {
    width: 165px;
    padding: 6px 12px 6px 34px;
    outline: none;
    border: 1px solid rgba(109, 109, 109, 0.4);
    border-right-width: 0;
    border-radius: 4px 0 0 4px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}

.inputLocation i {
    position: absolute;
    top: 10px;
    left: 12px;
    z-index: 1;
}

.inputSearchOptions {
    width: 496px;
    position: relative;
    /* border: 1px solid green; */
}

.inputSearchOptions input {
    width: 496px;
    padding: 6px 12px 6px 40px;
    outline: none;
    border: 1px solid rgba(109, 109, 109, 0.4);
    border-left-width: 0;
    border-radius: 0 4px 4px 0;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}

.inputSearchOptions i {
    position: absolute;
    top: 10px;
    left: 12px;
    z-index: 1;
}

