.top-header-right a {
    padding: 0 15px;
    margin-left: 20px;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    color: #333333;
}

.top-header-right button {
    margin-left: 20px;
    padding: 6px 25px;
    border-radius: 4px;
    border: 0;
}

.loginBtn {
    background-color: #333333;
    color: #fff;
}

@media screen and (max-width:768px) {
    .loginBtn {
        width: 350px !important;
        margin: auto;
    }
}